.container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: #fff;
  z-index: 99;
  color: #333;
}
.container .loading {
  height: 50px;
  line-height: 50px;
  text-align: center;
  color: #333;
}
.container .navbar {
  position: relative;
  width: 100%;
  height: 44px;
  line-height: 44px;
  font-size: 18px;
  color: #fff;
  background: #108ee9;
  text-align: center;
  font-weight: bold;
}
.container .navbar .iconfont {
  position: absolute;
  font-size: 25px;
  left: 15px;
  font-weight: normal;
}
.container .content {
  padding: 10px;
}
.container .content .title {
  height: 30px;
  line-height: 30px;
  font-size: 20px;
  color: #000;
}
.container .content .desc {
  line-height: 1.5;
  margin-top: 10px;
}
.container .comment-section .title {
  font-size: 14px;
  height: 25px;
  line-height: 25px;
  padding-left: 10px;
  margin-top: 10px;
  background-color: #eee;
}
.container .comment-section ul li {
  display: flex;
  padding: 10px 0;
}
.container .comment-section ul li .left {
  margin: 0 10px;
}
.container .comment-section ul li .left img {
  height: 35px;
  width: 35px;
  border-radius: 50%;
}
.container .comment-section ul li .right {
  flex-grow: 1;
  padding-bottom: 10px;
  padding-right: 10px;
  border-bottom: 1px solid #eee;
}
.container .comment-section ul li .right .user-box {
  display: flex;
  justify-content: space-between;
  color: #555;
}
.container .comment-section ul li .right .time {
  color: #777;
  font-size: 12px;
  margin: 5px 0 8px;
}
.container .comment-section ul li .right .comment {
  line-height: 1.5;
}
